import React, { Component, Children } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import Icon from '../../icon';

function mapIconSize(size) {
  return {
    large: 'large',
    medium: 'medium',
    small: 'medium',
    xs: 'medium',
  }[size];
}

/** Button */
export default class Button extends Component {
  static propTypes = {
    prefix: PropTypes.string,
    rtl: PropTypes.bool,
    /**
     * 按钮的类型
     */
    type: PropTypes.oneOf(['primary', 'secondary', 'normal']),
    /**
     * 按钮的尺寸
     */
    size: PropTypes.oneOf(['xs', 'small', 'medium', 'large']),
    /**
     * 按钮中 Icon 的尺寸，用于替代 Icon 的默认大小
     */
    iconSize: PropTypes.oneOf(['xs', 'small', 'medium', 'large', 'xl', 'xxl', 'xxxl']),
    /**
     * 当 component = 'button' 时，设置 button 标签的 type 值
     */
    htmlType: PropTypes.oneOf(['submit', 'reset', 'button']),
    /**
     * 设置标签类型
     */
    component: PropTypes.oneOf(['button', 'a', 'div', 'span']),
    /**
     * 设置按钮的载入状态
     */
    loading: PropTypes.bool,
    /**
     * 是否为幽灵按钮
     */
    ghost: PropTypes.oneOf([true, false, 'light', 'dark']),
    /**
     * 是否为文本按钮
     */
    text: PropTypes.bool,
    /**
     * 是否为警告按钮
     */
    warning: PropTypes.bool,
    /**
     * 是否禁用
     */
    disabled: PropTypes.bool,
    /**
     * 点击按钮的回调
     * @param {Object} e Event Object
     */
    onClick: PropTypes.func,
    className: PropTypes.string,
    onMouseUp: PropTypes.func,
    children: PropTypes.node,
  };

  static defaultProps = {
    prefix: 'next-',
    type: 'normal',
    size: 'medium',
    htmlType: 'button',
    component: 'button',
    loading: false,
    ghost: false,
    text: false,
    warning: false,
    disabled: false,
    onClick: () => {},
  };

  onMouseUp = e => {
    this.button.blur();

    if (this.props.onMouseUp) {
      this.props.onMouseUp(e);
    }
  };

  buttonRefHandler = button => {
    this.button = button;
  };

  render() {
    const {
      prefix,
      className,
      type,
      size,
      htmlType,
      loading,
      text,
      warning,
      ghost,
      component,
      iconSize,
      children,
      rtl,
      ...others
    } = this.props;
    const ghostType = ['light', 'dark'].indexOf(ghost) >= 0 ? ghost : 'dark';

    const btnCls = classNames({
      [`${prefix}btn`]: true,
      [`${prefix}${size}`]: size,
      [`${prefix}btn-${type}`]: type && !ghost,
      [`${prefix}btn-text`]: text,
      [`${prefix}btn-warning`]: warning,
      [`${prefix}btn-loading`]: loading,
      [`${prefix}btn-ghost`]: ghost,
      [`${prefix}btn-${ghostType}`]: ghost,
      [className]: className,
    });

    const count = Children.count(children);
    const clonedChildren = Children.map(children, (child, index) => {
      if (child && typeof child.type === 'function' && child.type._typeMark === 'icon') {
        const iconCls = classNames({
          [`${prefix}btn-icon`]: !iconSize, // 如果用户没有传 iconSize，则使用该样式标记 icon 为 button 预设尺寸
          [`${prefix}icon-first`]: count > 1 && index === 0,
          [`${prefix}icon-last`]: count > 1 && index === count - 1,
          [`${prefix}icon-alone`]: count === 1,
          [child.props.className]: !!child.props.className,
        });
        return React.cloneElement(child, {
          className: iconCls,
          size: iconSize || mapIconSize(size),
        });
      }

      // 两个字的中文中间添加间距
      // if (count === 1 && typeof child === 'string' && /^[\u4e00-\u9fa5]{2}$/.test(child)) {
      //   return <>{child[0]}<span className={`${prefix}btn-space`} />{child[1]}</>;
      // }

      return child;
    });

    const TagName = component;
    const tagAttrs = {
      ...others,
      type: htmlType,
      className: btnCls,
    };

    if (TagName !== 'button') {
      delete tagAttrs.type;

      if (tagAttrs.disabled) {
        delete tagAttrs.onClick; // a 标签的 onClick 浏览器默认不会禁用
        tagAttrs.href && delete tagAttrs.href; // a 标签在禁用状态下无跳转
      }
    }

    return (
      <TagName
        {...tagAttrs}
        dir={rtl ? 'rtl' : undefined}
        onMouseUp={this.onMouseUp}
        ref={this.buttonRefHandler}
      >
        <span className={`${prefix}btn-children`}>{clonedChildren}</span>
        {loading ? <span className={`${prefix}btn-loading-container`}><Icon type="loading" size={iconSize || mapIconSize(size)} /></span> : null}
      </TagName>
    );
  }
}
